@import "../../styles/variables.scss";

.heroes {
  padding: 50px 0;
  display: flex;
  gap: 25px;
  position: relative;

  button {
    display: flex;
    justify-content: center;
  }
}

.heroes__side {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.char {
  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__content {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 650px 425px;
    column-gap: 25px;
    align-items: start;
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 200px);
    column-gap: 25px;
    row-gap: 30px;
    margin-bottom: 20px;
  }
  &__item {
    width: 200px;
    height: 318px;
    background-color: $dark;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
    padding: 15px;
    cursor: pointer;
    transition: 0.3s transform;
    img {
      width: 200px;
      height: 200px;
      object-fit: cover;
      transform: translate(-15px, -15px);
    }
    &_selected {
      box-shadow: 0 5px 20px $main-color;
      transform: translateY(-8px);
    }
  }
  &__name {
    font-weight: bold;
    font-size: 22px;
    line-height: 29px;
    text-transform: uppercase;
    color: #fff;
  }
}
